@import url(https://fonts.googleapis.com/css2?family=Exo:wght@600&amp;display=swap);
/*! minireset.css v0.0.6 | MIT License | github.com/jgthms/minireset.css */html,body,p,ol,ul,li,dl,dt,dd,blockquote,figure,fieldset,legend,textarea,pre,iframe,hr,h1,h2,h3,h4,h5,h6{margin:0;padding:0}h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal}ul{list-style:none}button,input,select,textarea{margin:0}html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}img,video{height:auto;max-width:100%}iframe{border:0}table{border-collapse:collapse;border-spacing:0}td,th{padding:0}td:not([align]),th:not([align]){text-align:left}:root{--clr-1: #F4FCFB;--clr-2: #050E12;--ff-heading: 'Crete Round', serif;--ff-body: 'Exo', sans-serif}html{background:linear-gradient(to bottom, #F4FCFB, rgba(243,255,255,0.63));background:linear-gradient(to bottom, var(--clr-1), rgba(243,255,255,0.63))}body{min-height:100vh;font-family:Arial, Helvetica, sans-serif;position:relative}.wrapper{height:100vh;width:100vw}

.landing{display:flex;flex-direction:column;flex-wrap:wrap;margin-right:auto;margin-left:auto;padding:0 2rem;max-width:1140px;height:100%}@media (min-width: 768px){.landing{align-items:center;flex-direction:row}}.hello{margin-top:2rem;text-transform:uppercase;font-family:var(--ff-body)}@media (min-width: 768px){.hello{margin-top:0}}.hello h1{overflow:hidden;line-height:1.3}.hello h1 span{display:block;color:var(--clr-2);text-transform:uppercase;font-size:8vh;animation:animate 1s cubic-bezier(0.2, 0.6, 0.2, 1);animation-fill-mode:backwards}@media (min-width: 768px){.hello h1 span{font-size:4rem}}.hello h1:nth-child(3) span{color:#006b63}.logo{order:-12;margin-right:auto;margin-left:auto;padding:2rem 0;max-width:50%;animation:fade 0.6s cubic-bezier(0.33, 1, 0.68, 1);animation-delay:1s;animation-fill-mode:backwards}@media (min-width: 768px){.logo{order:initial;margin-right:0;margin-right:initial}}.logo svg{width:60px;height:60px}@media (min-width: 768px){.logo svg{width:350px;height:350px}}.footer{align-self:flex-end;margin-top:auto;margin-bottom:2rem;width:100%;text-align:center;animation:fade 0.6s cubic-bezier(0.33, 1, 0.68, 1);animation-delay:1.2s;animation-fill-mode:backwards}.footer h4{margin-bottom:1rem}.footer-rrss{display:flex;align-items:center;justify-content:center;padding-left:0}.footer-rrss li{vertical-align:bottom}.footer-rrss li a{display:inline-block;transition:all 0.32s ease;text-decoration:none}.footer-rrss li a:hover{opacity:0.5}.footer-rrss li:not(:last-child){margin-right:0.75rem}.animate-delay-1{animation-delay:0.2s !important}.animate-delay-2{animation-delay:0.4s !important}.animate-delay-3{animation-delay:0.6s !important}@keyframes animate{0%{opacity:0;transform:translateY(30vh)}100%{opacity:1;transform:translateY(0%)}}@keyframes fade{0%{opacity:0}100%{opacity:1}}

